import { Component, Injector, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute } from '@angular/router';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ConsultaProduccionServiceProxy,
  DetalleGeneralOutputDto,
  DetallePolizaRiesgoOutputDto,
  PlanPagoPolizaOutputDto,
  PolizaBitacoraOutputDto
} from '@shared/service-proxies/service-proxies';
import { CoberturaComponent } from '../../consultas-produccion/cobertura/cobertura.component';
import { BitacoracuotaComponent } from '../bitacoracuota/bitacoracuota.component';

@Component({
  selector: 'app-detalle',
  templateUrl: './detalle.component.html',
  styleUrls: ['./detalle.component.scss']
})
export class DetalleComponent extends AppComponentBase implements OnInit {
  dataDetalleGeneral = new DetalleGeneralOutputDto();
  dataDetallePolizaRiesgo: DetallePolizaRiesgoOutputDto[] = [];
  dataSourceBitacoraPoliza: MatTableDataSource<PolizaBitacoraOutputDto>;
  dataSourcePlanPago: MatTableDataSource<PlanPagoPolizaOutputDto>;

  displayedColumnsBitacoraPoliza: string[] = ['index', 'fecha', 'hora', 'motivo', 'descripcion'];

  displayedColumnsPlanPago: string[] = ['index', 'fechaVencimiento', 'valorCuota', 'estado', 'iconseleccion'];
  tamaoPopup = '750px';
  idPoliza = 0;
  snackBarDuration = 3000;

  constructor(
    injector: Injector,
    private _consultaProduccionService: ConsultaProduccionServiceProxy,
    private snackBar: MatSnackBar,
    private dialog: MatDialog,
    private route: ActivatedRoute
  ) {
    super(injector);
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.idPoliza = params['id'] || 0;
      this.cargarDatos(this.idPoliza);
    });
  }

  cargarDatos(idPoliza: number) {
    this._consultaProduccionService.detallePolizaGeneral(idPoliza).subscribe(
      (result: DetalleGeneralOutputDto[]) => {
        if (result && result.length > 0) {
          this.dataDetalleGeneral = result[0];
        } else {
          this.dataDetalleGeneral = new DetalleGeneralOutputDto();
        }
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );

    this._consultaProduccionService.detallePolizaRiesgoVeh(idPoliza).subscribe(
      (result: DetallePolizaRiesgoOutputDto[]) => {
        this.dataDetallePolizaRiesgo = result;
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );

    this._consultaProduccionService.bitacoraPoliza(idPoliza).subscribe(
      (result: PolizaBitacoraOutputDto[]) => {
        this.dataSourceBitacoraPoliza = new MatTableDataSource<PolizaBitacoraOutputDto>(result);
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );

    this._consultaProduccionService.planPagoPoliza(idPoliza).subscribe(
      (result: PlanPagoPolizaOutputDto[]) => {
        this.dataSourcePlanPago = new MatTableDataSource<PlanPagoPolizaOutputDto>(result);
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );
  }

  openDialogCoberturas(DetallePolizaRiesgo: DetallePolizaRiesgoOutputDto) {
    this.dialog.open(CoberturaComponent, {
      width: this.tamaoPopup,
      data: DetallePolizaRiesgo
    });
  }

  openDialogCuotas(idCuota: number) {
    this.dialog.open(BitacoracuotaComponent, {
      width: this.tamaoPopup,
      data: idCuota || 0
    });
  }
}
