import { Routes } from '@angular/router';
import { AppComponent } from '@app/app.component';
import { DashboardComponent } from '@app/dashboard/dashboard.component';
import { TenantsComponent } from '@app/tenants/tenants.component';
import { AppRouteGuard } from '@shared/auth/auth-route-guard';
import { ConsultaCotizacionComponent } from './consultas/consultacotizacion/consultacotizacion.component';
import { DetalleCotizacionComponent } from './consultas/consultacotizacion/detallecotizacion/detallecotizacion.component';
import { DetalleComponent } from './consultas/consultas-produccion/detalle/detalle.component';
import { ProduccionComponent } from './consultas/consultas-produccion/produccion/produccion.component';
import { ErrorPageComponent } from './error-page/error-page.component';
import { LaboratorioComponent } from './laboratorio/laboratorio.component';
import { Laboratorio2Component } from './laboratorio2/laboratorio2.component';
import { FlujoDinamicoURLResolve } from './sales-flow/flujo-dinamico-url.resolve';
import { FlujoDinamicoResolve } from './sales-flow/flujo-dinamico.resolve';
import { LauncherComponent } from './sales-flow/launcher/launcher.component';
import { SalesFlowComponent } from './sales-flow/sales-flow.component';
import { SimulationSearchEngineComponent } from './sales-flow/simulation-search-engine/simulation-search-engine.component';
import { SummaryComponent } from './sales-flow/summary/summary.component';

export const routes: Routes = [
  {
    path: '',
    component: AppComponent,
    children: [
      { path: 'home', component: DashboardComponent, canActivate: [AppRouteGuard] },
      { path: 'laboratorio', component: LaboratorioComponent },
      { path: 'laboratorio2', component: Laboratorio2Component },
      // { path: 'users', component: UsersComponent, data: { permission: 'Pages.Users' }, canActivate: [AppRouteGuard] },
      // { path: 'roles', component: RolesComponent, data: { permission: 'Pages.Roles' }, canActivate: [AppRouteGuard] },
      { path: 'tenants', component: TenantsComponent, data: { permission: 'Host.Administrar.Tenants' }, canActivate: [AppRouteGuard] },
      { path: 'sales-flow', component: SalesFlowComponent },
      { path: 'sales-flow/summary', component: SummaryComponent },
      { path: 'launcher', component: LauncherComponent, canActivate: [AppRouteGuard] },
      { path: 'error-page', component: ErrorPageComponent, canActivate: [AppRouteGuard] },
      { path: 'consultas/produccion', component: ProduccionComponent, canActivate: [AppRouteGuard] },
      { path: 'consultas/consultacotizacion', component: ConsultaCotizacionComponent, canActivate: [AppRouteGuard] },
      { path: 'consultas/detalle/:id', component: DetalleComponent },
      { path: 'consultas/consultacotizacion/detallecotizacion/:id', component: DetalleCotizacionComponent },
      {
        path: 'simulation-search-engine',
        component: SimulationSearchEngineComponent,
        resolve: {
          flujoDinamico: FlujoDinamicoResolve
        }
      },
      {
        path: 'simulation-search-engine/:id',
        component: SimulationSearchEngineComponent,
        resolve: {
          flujoDinamico: FlujoDinamicoURLResolve
        }
      },
      {
        path: 'perfil',
        loadChildren: () => import('./perfil/perfil.module').then(m => m.PerfilModule)
      },
      {
        path: 'usuarios',
        loadChildren: () => import('./usuarios/usuarios.module').then(m => m.UsuariosModule)
      },
      {
        path: 'roles',
        loadChildren: () => import('./roles/roles.module').then(m => m.RolesModule)
      }
    ]
  }
];
