import { Component, Injector, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
// import { BitacoracuotaComponent } from '../bitacoracuota/bitacoracuota.component';
import { ActivatedRoute } from '@angular/router';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ConsultaCotizacionServiceProxy,
  CotizacionBitacoraOutputDto,
  DetalleCotizacionRiesgoOutputDto,
  DetalleGeneralCotizacionOutputDto,
  PlanPagoPolizaOutputDto
} from '@shared/service-proxies/service-proxies';
import { DetalleplancotizacionComponent } from '../detalleplancotizacion/detalleplancotizacion.component';

@Component({
  selector: 'app-detallecotizacion',
  templateUrl: './detallecotizacion.component.html',
  styleUrls: ['./detallecotizacion.component.scss']
})
export class DetalleCotizacionComponent extends AppComponentBase implements OnInit {
  dataDetalleGeneral = new DetalleGeneralCotizacionOutputDto();
  dataDetalleCotizacionRiesgo: DetalleCotizacionRiesgoOutputDto[] = [];
  dataSourceBitacoraCotizacion: MatTableDataSource<CotizacionBitacoraOutputDto>;
  dataSourcePlanPago: MatTableDataSource<PlanPagoPolizaOutputDto>;

  displayedColumnsBitacoraPoliza: string[] = ['index', 'fecha', 'hora', 'motivo', 'descripcion'];

  displayedColumnsPlanPago: string[] = ['index', 'fechaVencimiento', 'valorCuota', 'estado', 'iconseleccion'];
  tamaoPopup = '750px';
  idCotizacion = 0;
  snackBarDuration = 3000;

  constructor(
    injector: Injector,
    private _consultaCotizacionService: ConsultaCotizacionServiceProxy,
    private snackBar: MatSnackBar,
    private dialog: MatDialog,
    private route: ActivatedRoute
  ) {
    super(injector);
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.idCotizacion = params['id'] || 0;
      this.cargarDatos(this.idCotizacion);
    });
  }

  cargarDatos(idCotizacion: number) {
    this._consultaCotizacionService.detalleGeneralCotizacion(idCotizacion).subscribe(
      (result: DetalleGeneralCotizacionOutputDto[]) => {
        if (result && result.length > 0) {
          this.dataDetalleGeneral = result[0];
        } else {
          this.dataDetalleGeneral = new DetalleGeneralCotizacionOutputDto();
        }
        console.log(this.dataDetalleGeneral);
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );

    this._consultaCotizacionService.detalleCotizacionRiesgo(idCotizacion).subscribe(
      (result: DetalleCotizacionRiesgoOutputDto[]) => {
        this.dataDetalleCotizacionRiesgo = result;
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );

    this._consultaCotizacionService.bitacoraCotizacion(idCotizacion).subscribe(
      (result: CotizacionBitacoraOutputDto[]) => {
        this.dataSourceBitacoraCotizacion = new MatTableDataSource<CotizacionBitacoraOutputDto>(result);
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      }
    );
  }

  /*this._consultaCotizacionService.planPagoPoliza(idPoliza).subscribe(
      (result: PlanPagoPolizaOutputDto[]) => {
        this.dataSourcePlanPago = new MatTableDataSource<PlanPagoPolizaOutputDto>(result);
      },
      err => {
        this.snackBar.open(this.l('consult.detail.loaderror'), '', { duration: this.snackBarDuration });
      });

  }
*/
  openDialogCoberturas(DetallePolizaRiesgo: DetalleCotizacionRiesgoOutputDto) {
    this.dialog.open(DetalleplancotizacionComponent, {
      width: this.tamaoPopup,
      data: DetallePolizaRiesgo
    });
  }
  /*
  openDialogCuotas(idCuota: number) {
    this.dialog.open(BitacoracuotaComponent, {
      width: this.tamaoPopup,
      data: idCuota || 0
    });
  }
*/
}
