import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, ActivatedRoute } from '@angular/router';
import { FlujoDinamicoServiceProxy } from '@shared/service-proxies/service-proxies';

@Injectable()
export class FlujoDinamicoURLResolve implements Resolve<any> {
    constructor(private _flujoDinamicoServiceProxy: FlujoDinamicoServiceProxy,
                private _route: ActivatedRoute) {}

    resolve(route: ActivatedRouteSnapshot) {
        sessionStorage.setItem('flujoId', route.paramMap.get('id'));
        return this._flujoDinamicoServiceProxy.obtener(Number(route.paramMap.get('id')));
    }
}
