  cargarListaTiposDeArchivo() {
    this._formularioDinamicoServiceProxy.obtenerLista(Number(this.flujoId), 30, 0).subscribe(data => {
      this.comboDataFiles = data;
    });
  }

  cargarExpresionesRegulares() {
    this._regExpServiceProxy.listar().subscribe(data => {
      this.regularExpresions = data;
      console.log(this.regularExpresions);
    });
  }

  buscaRegExp(key): string {
    if (key) {
      for (const regexp of this.regularExpresions) {
        if (Number(regexp.id) === Number(key)) {
          return regexp.expresion;
        }
      }
    } else {
      return '';
    }
  }

  buscaRegExpErrorMessage(key): string {
    if (key) {
      for (const regexp of this.regularExpresions) {
        if (Number(regexp.id) === Number(key)) {
          return regexp.mensaje;
        }
      }
    } else {
      return '';
    }
  }
