  formatValue(value: number) {
    if (this.simboloMoneda === 'CLP') {
      return value.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.');
    } else if (this.simboloMoneda === 'USD') {
      // return value.toString().replace(/(\d)(?=(\d{2})+(?!\d))/g, '$1,')
    } else {
      return value;
    }
  }

  setFiltros() {
    this.planes = [];
    this.companias = [];
    this.deducibles = [];
    // this.filterChecked = 0;
    for (const offer of this.offers.tarifas) {
      if (offer.visible) {
        if (this.planes.indexOf(offer.plan) === -1) {
          this.planes.push(offer.plan);
        }
        if (this.companias.indexOf(offer.compania) === -1) {
          this.companias.push(offer.compania);
        }
        if (this.deducibles.indexOf(offer.deducible) === -1) {
          this.deducibles.push(offer.deducible);
        }
        const primaPaso = Math.ceil(offer.prima);
        if (this.valorCuota.indexOf(primaPaso) === -1) {
          this.valorCuota.push(primaPaso);
        }
      }
    }
    // VALOR CUOTAS (PRIMAS) MINIMA Y MAXIMA
    this.cuotaMaximo = this.valorCuota[0]
      .toString()
      .replace('.', '')
      .replace(',', '');
    this.cuotaMinimo = this.valorCuota[0]
      .toString()
      .replace('.', '')
      .replace(',', '');
    for (const cuota of this.valorCuota) {
      if (
        Number(
          cuota
            .toString()
            .replace('.', '')
            .replace(',', '')
        ) > Number(this.cuotaMaximo)
      ) {
        this.cuotaMaximo = cuota
          .toString()
          .replace('.', '')
          .replace(',', '');
      }
      if (
        Number(
          cuota
            .toString()
            .replace('.', '')
            .replace(',', '')
        ) < Number(this.cuotaMinimo)
      ) {
        this.cuotaMinimo = cuota
          .toString()
          .replace('.', '')
          .replace(',', '');
      }
    }
    // SETEAR MAXIMO Y MINIMO A NUMEROS DIVISIBLES POR 100
    let maximo = Number(this.cuotaMaximo);
    do {
      if (maximo % 100 === 0) {
        break;
      }
      maximo++;
    } while (maximo % 100 !== 0);
    this.cuotaMaximo = maximo.toString();
    let minimo = Number(this.cuotaMinimo);
    do {
      if (minimo % 100 === 0 || minimo < 1) {
        break;
      }
      minimo--;
    } while (minimo % 100 !== 0);
    this.cuotaMinimo = minimo.toString();
    // SIMBOLO MONEDA
    /*     this.simboloMoneda = this.offers.result.offercards[0].moneda;
            if (this.simboloMoneda === 'CLP') {
              this.simbolo = '$';
            } else if (this.simboloMoneda === 'USD') {
              this.simbolo = 'US$';
            } else if (this.simboloMoneda === 'UF') {
              this.simbolo = 'UF';
            } */
  }

  updateOffers(checked: boolean, filtro: string, value: string, cuotaValue: number) {
    cuotaValue = 99999;
    // ARRAY DE FILTROS
    if (filtro === 'deducible') {
      if (checked) {
        this.filterDeduc.push(value);
      } else {
        this.filterDeduc.splice(this.filterDeduc.indexOf(value), 1);
      }
    } else if (filtro === 'plan') {
      if (checked) {
        this.filterPlan.push(value);
      } else {
        this.filterPlan.splice(this.filterPlan.indexOf(value), 1);
      }
    } else if (filtro === 'compania') {
      if (checked) {
        this.filterCia.push(value);
      } else {
        this.filterCia.splice(this.filterCia.indexOf(value), 1);
      }
    }
    // SI HAY FILTROS TODOS LOS CARDS NO VISIBLES, SI NO HAY FILTROS TODOS LOS CARDS VISIBLES
    for (const offer of this.offers.tarifas) {
      if (this.filterDeduc.length >= 1 || this.filterPlan.length >= 1 || this.filterCia.length >= 1) {
        offer.visible = false;
        offer.mcaSeleccionado = false;
      } else {
        offer.visible = true;
        offer.mcaSeleccionado = true;
      }
    }
    // CARD VISIBLE SI ESTA EN EL ARRAY
    if (this.filterDeduc.length >= 1 && this.filterPlan.length === 0 && this.filterCia.length === 0) {
      // TIENE SOLO FILTRO DE DEDUCIBLE
      for (const offer of this.offers.tarifas) {
        if (this.filterDeduc.indexOf(offer.deducible) !== -1) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length === 0 && this.filterPlan.length >= 1 && this.filterCia.length === 0) {
      // TIENE SOLO FILTRO DE PLAN
      for (const offer of this.offers.tarifas) {
        if (this.filterPlan.indexOf(offer.plan) !== -1) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length === 0 && this.filterPlan.length === 0 && this.filterCia.length >= 1) {
      // TIENE SOLO FILTRO DE CIA
      for (const offer of this.offers.tarifas) {
        if (this.filterCia.indexOf(offer.compania) !== -1) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length >= 1 && this.filterPlan.length >= 1 && this.filterCia.length === 0) {
      // TIENE FILTROS DE DEDUCIBLE Y PLAN
      for (const offer of this.offers.tarifas) {
        if (this.filterDeduc.indexOf(offer.deducible) !== -1 && this.filterPlan.indexOf(offer.plan) !== -1) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length >= 1 && this.filterPlan.length === 0 && this.filterCia.length >= 1) {
      // TIENE FILTROS DE DEDUCIBLE Y CIA
      for (const offer of this.offers.tarifas) {
        if (this.filterDeduc.indexOf(offer.deducible) !== -1 && this.filterCia.indexOf(offer.compania) !== -1) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length === 0 && this.filterPlan.length >= 1 && this.filterCia.length >= 1) {
      // TIENE FILTROS DE PLAN Y CIA
      for (const offer of this.offers.tarifas) {
        if (this.filterPlan.indexOf(offer.plan) !== -1 && this.filterCia.indexOf(offer.compania) !== -1) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length >= 1 && this.filterPlan.length >= 1 && this.filterCia.length >= 1) {
      // TIENE FILTROS DE DEDUCIBLE, PLAN Y CIA
      for (const offer of this.offers.tarifas) {
        // tslint:disable-next-line:max-line-length
        if (
          this.filterDeduc.indexOf(offer.deducible) !== -1 &&
          this.filterPlan.indexOf(offer.plan) !== -1 &&
          this.filterCia.indexOf(offer.compania) !== -1
        ) {
          offer.visible = true;
          offer.mcaSeleccionado = true;
        }
      }
    } else if (this.filterDeduc.length === 0 && this.filterPlan.length === 0 && this.filterCia.length === 0) {
      // NO TIENE FILTROS DE DEDUCIBLE, PLAN Y CIA. FILTRAR SOLO POR VALOR CUOTA.
      // PRIMERO, DEJAR TODOS NO VISIBLES
      for (const offer of this.offers.tarifas) {
        offer.visible = false;
        offer.mcaSeleccionado = false;
      }
      // RECORRER
      for (const offer of this.offers.tarifas) {
        offer.visible = true;
        offer.mcaSeleccionado = true;
      }
    }
  }

