  obtenerEntidadValor(entidad: string) {
    for (const seccion of this.flujo.secciones) {
      for (const contenedor of seccion.contenedores) {
        if (
          contenedor.tipoContenedor !== 'CARD-INPUT' &&
          contenedor.tipoContenedor !== 'ACCORDION-INPUT' &&
          contenedor.tipoContenedor !== 'ACCORDION-FORM' &&
          contenedor.tipoContenedor !== 'OFFER-GRID'
        ) {
          for (const field of contenedor.campos) {
            if (field.entidad === entidad) {
              return field.valor;
            }
          }
        }
      }
    }
  }

  obtenerEntidadRiesgoValor(entidad: string, subcontenedor: ContenedorDto) {
    for (const field of subcontenedor.campos) {
      if (field.entidad === entidad) {
        return field.valor;
      }
    }
  }

