import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AppComponentBase } from '@shared/app-component-base';
import {
  DetallePlanCoberturaOutputDto,
  DetallePolizaRiesgoOutputDto,
  DetallePolizaRiesgoPlanOutputDto
} from '@shared/service-proxies/service-proxies';

@Component({
  selector: 'app-cobertura',
  templateUrl: './cobertura.component.html',
  styleUrls: ['./cobertura.component.scss']
})
export class CoberturaComponent extends AppComponentBase implements OnInit {
  datosPlan: DetallePolizaRiesgoPlanOutputDto;
  detalleCoberturas: DetallePlanCoberturaOutputDto[] = [];

  displayedColumnsDetallePlan: string[] = ['cobertura', 'sumaAsegurada', 'deducible'];

  constructor(
    injector: Injector,
    public dialogRef: MatDialogRef<CoberturaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DetallePolizaRiesgoOutputDto | any
  ) {
    super(injector);
    if (data) {
      if (data.datosPlan && data.datosPlan.length > 0) {
        this.datosPlan = data.datosPlan[0];
      } else {
        this.datosPlan = new DetallePolizaRiesgoPlanOutputDto();
      }

      if (this.data.detalleCobertura) {
        this.detalleCoberturas = data.detalleCobertura;
      }
    }
  }

  ngOnInit() {}

  onNoClick(): void {
    this.dialogRef.close();
  }
}
