import { Component, OnInit, ViewChild, AfterViewInit, ElementRef, Injector } from '@angular/core';
// tslint:disable-next-line:max-line-length
import { ConsultaProduccionServiceProxy, ConsultaProduccionInputDto, ConsultaProduccionOutputDto,
  FiltroGeneralOutputDto } from '@shared/service-proxies/service-proxies';
import { ValidatorFn, FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import * as moment from 'moment';
import { ExportExcelComponent } from '@shared/plus-exportExcel/plus-exportExcel.component';
import { AppComponentBase } from '@shared/app-component-base';
import { MatPaginator, MatTableDataSource, MatSort, MatSnackBar, MatDialog, MatSelect } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { RutPipe } from 'ng2-rut';
import { rutValidate } from 'rut-helpers';
import { CambiarestadoComponent } from '../cambiarestado/cambiarestado.component';
import { CambiarestadoConfirmacionComponent } from '../cambiarestado-confirmacion/cambiarestado-confirmacion.component';

export interface DialogDataForm {
  estado: number;
  estadoDescripcion: string;
  motivo: number;
  observacion: string;
}

export interface DialogData {
  selected: ConsultaProduccionOutputDto[];
  form: DialogDataForm;
}

const notEmptyString = (value: string) => {
  return (value || '').trim().length > 0;
}

const notEmpty = (control: AbstractControl) => {
  return control ? notEmptyString(control.value) : false;
}

function rutValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } => {
    if (notEmpty(control) && !rutValidate(control.value)) {
      return { 'rut':  true  }
    } else {
      return null;
    }
  };
}

function tipoPersonaValidator(val1: string, val2: string, tipoPersona: number): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } => {
    if (tipoPersona === 0 && (notEmptyString(val1) || notEmptyString(val2))) {
      return { 'tipoPersona':  true  }
    } else {
      return null;
    }
  };
}

function rangoFechasValidator(desde: any, hasta: any): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } => {
    let startDate: Date;
    let endDate: Date;
    const start = typeof desde === 'object' ? desde.format('YYYY-MM-DD') : desde;
    const end = typeof hasta === 'object' ? hasta.format('YYYY-MM-DD') : hasta;
    const isDate = moment(start, 'YYYY-MM-DD', false).isValid() && moment(end, 'YYYY-MM-DD', false).isValid();
    if (isDate) {
      startDate = new Date(start);
      endDate = new Date(end);
    }

    if (start && end && isDate && startDate <= endDate) {
      return null;
    } else {
      return { 'desde':  true  }
    }
  };
}

const almenosUnElementoValidator: ValidatorFn = (form: FormGroup) => {
  const nroPoliza: boolean = notEmpty(form.get('nroPoliza'));
  const nroCotizacion: boolean = notEmpty(form.get('nroCotizacion'));
  const nroMotorVehiculo: boolean = notEmpty(form.get('nroMotorVehiculo'));
  const nroChasisVehiculo: boolean = notEmpty(form.get('nroChasisVehiculo'));
  const patenteVehiculo: boolean = notEmpty(form.get('patenteVehiculo'));
  const nroFacturaVehiculo: boolean = notEmpty(form.get('nroFacturaVehiculo'));

  const nroDocumentoPersona: boolean = notEmpty(form.get('nroDocumentoPersona'));
  const nombreCompletoPersona: boolean = notEmpty(form.get('nombreCompletoPersona'));

  if (nroPoliza || nroCotizacion || nroDocumentoPersona || nombreCompletoPersona || nroMotorVehiculo
    || nroChasisVehiculo || patenteVehiculo || nroFacturaVehiculo) {
      return null;
    } else {
      return { almenosUnElemento: true };
    }
}

@Component({
  selector: 'app-produccion',
  templateUrl: './produccion.component.html',
  styleUrls: ['./produccion.component.scss']
})
export class ProduccionComponent extends AppComponentBase implements OnInit {
  @ViewChild('endtable', { static: true }) endtable: ElementRef;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild('selectFiltroTipoFecha', { static: true }) selectFiltroTipoFecha: MatSelect;
  @ViewChild('selectFiltroEstadoPoliza', { static: true }) selectFiltroEstadoPoliza: MatSelect;
  @ViewChild('selectFiltroNegocios', { static: true }) selectFiltroNegocios: MatSelect;
  @ViewChild('selectFiltroCanales', { static: true }) selectFiltroCanales: MatSelect;
  @ViewChild('selectFiltroSucursales', { static: true }) selectFiltroSucursales: MatSelect;
  @ViewChild('selectFiltroAgentes', { static: true }) selectFiltroAgentes: MatSelect;
  @ViewChild('selectFiltroMarcas', { static: true }) selectFiltroMarcas: MatSelect;
  @ViewChild('selectFiltroModelos', { static: true }) selectFiltroModelos: MatSelect;
  @ViewChild('selectFiltroAnnos', { static: true }) selectFiltroAnnos: MatSelect;
  @ViewChild('selectFiltroUsoEspecifico', { static: true }) selectFiltroUsoEspecifico: MatSelect;
  @ViewChild('selectFiltroEstado', { static: true }) selectFiltroEstado: MatSelect;
  @ViewChild('selectFiltroCompannias', { static: true }) selectFiltroCompannias: MatSelect;
  @ViewChild('selectFiltroRamos', { static: true }) selectFiltroRamos: MatSelect;
  @ViewChild('selectFiltroPlanes', { static: true }) selectFiltroPlanes: MatSelect;
  @ViewChild('selectFiltroTipoPersona', { static: true }) selectFiltroTipoPersona: MatSelect;
  exportExcelComponent: ExportExcelComponent;
  formConsultaGeneral: FormGroup;
  formConsultaEspecifica: FormGroup;
  formCache: any;
  defaultItemSelect: FiltroGeneralOutputDto;
  snackBarDuration = 3000;
  contadorConsultas = 0;
  loading = false;
  loadingPending = 14;
  criterioGeneral = 0;

  paginatorLength = 100;
  paginatorPageSize = 10;
  paginatorPageSizeOptions: number[] = [5, 10, 25, 100];
  paginatorPageIndex = 0;
  tamaoPopup = '750px';
  tamaoPopupConfirmacion = '550px';

  displayedColumns: string[] = [
    'select',
    'nro',
    'nroPoliza',
    'compania',
    'estado',
    'fechaVigenciaPolizaIni',
    'nombreContratante',
    'agente',
    'canal',
    'iconseleccion'
  ];
  dataSource: any;
  data: ConsultaProduccionOutputDto[];
  selection = new SelectionModel<ConsultaProduccionOutputDto>(true, []);

  // Fechas
  filtroTipoFecha: FiltroGeneralOutputDto[];
  // Documentos
  filtroEstadoPoliza: FiltroGeneralOutputDto[];
  // Datos comerciales
  filtroNegocios: FiltroGeneralOutputDto[];
  filtroCanales: FiltroGeneralOutputDto[];
  filtroSucursales: FiltroGeneralOutputDto[];
  filtroAgentes: FiltroGeneralOutputDto[];
  // Datos vehiculos
  filtroMarcas: FiltroGeneralOutputDto[];
  filtroModelos: FiltroGeneralOutputDto[];
  filtroAnnos: FiltroGeneralOutputDto[];
  filtroUsoEspecifico: FiltroGeneralOutputDto[];
  filtroEstado: FiltroGeneralOutputDto[];
  // Datos vehiculos
  filtroCompannias: FiltroGeneralOutputDto[];
  filtroRamos: FiltroGeneralOutputDto[];
  filtroPlanes: FiltroGeneralOutputDto[];
  // Datos Persona
  filtroTipoPersona: FiltroGeneralOutputDto[];

  constructor(injector: Injector,
              private _consultaProduccionService: ConsultaProduccionServiceProxy,
              private formBuilder: FormBuilder,
              private snackBar: MatSnackBar,
              private dialog: MatDialog) {
    super(injector);
    this.exportExcelComponent = new ExportExcelComponent(injector);
  }

  ngOnInit() {
    this.defaultItemSelect = {id: 0, nombre: this.l('consult.production.all')} as FiltroGeneralOutputDto;
    this.definicionConsultaGeneral();
    this.definicionConsultaEspecifica();
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

  loadingFlag() {
    return this.loading || this.loadingPending > 0;
  }

  definicionConsultaGeneral() {
    this.formConsultaGeneral = this.formBuilder.group({
      // Fechas
      tipoFecha: [null, [Validators.required] ],
      fechaDesde: [ moment().subtract(2, 'months').format('YYYY-MM-DD'), [Validators.required] ],
      fechaHasta: [ moment().subtract(0, 'months').format('YYYY-MM-DD'), [Validators.required] ],
      // Documentos
      estadoPolizaId: [null],
      // Datos Comerciales
      negocioId: [null],
      canalId: [null],
      sucursalId: [null],
      agenteId: [null],
      // Datos Vehiculos
      marcaVehiculoId: [null],
      modeloVehiculoId: [null],
      anioVehiculo: [null],
      usoVehiculoId: [null],
      estadoVehiculoId: [null],
      // Datos Emision
      companiaId: [null],
      ramoId: [null],
      planId: [null]
    });
    this.cargarFiltrosConsultaGeneral();
  }

  definicionConsultaEspecifica() {
    this.formConsultaEspecifica = this.formBuilder.group({
      // Documento
      nroPoliza: [null, [Validators.maxLength(32)] ],
      nroCotizacion: [null, [Validators.maxLength(32)] ],
      // Datos Persona
      tipoPersonaId: [null],
      nroDocumentoPersona: [null, [rutValidator(), Validators.maxLength(32)] ],
      nombreCompletoPersona: [null, [Validators.maxLength(256)] ],
      // Datos Vehculo
      nroMotorVehiculo: [null, [Validators.maxLength(32)] ],
      nroChasisVehiculo: [null, [Validators.maxLength(32)] ],
      patenteVehiculo: [null, [Validators.maxLength(16)] ],
      nroFacturaVehiculo: [null, [Validators.maxLength(32)] ],
    }, {validator: almenosUnElementoValidator});
    this.cargarFiltrosConsultaEspecifica();
  }

  limpiarConsultaGeneral() {
    this.formConsultaGeneral.reset();
    if (this.filtroTipoFecha && this.filtroTipoFecha.length > 1) {
      this.formConsultaGeneral.controls.tipoFecha.setValue(this.filtroTipoFecha[1].id);
    } else {
      this.formConsultaGeneral.controls.tipoFecha.setValue(this.filtroTipoFecha[0].id);
    }
    this.formConsultaGeneral.controls.fechaDesde.setValue(moment().subtract(2, 'months').format('YYYY-MM-DD'));
    this.formConsultaGeneral.controls.fechaHasta.setValue(moment().subtract(0, 'months').format('YYYY-MM-DD'));
    this.formConsultaGeneral.controls.estadoPolizaId.setValue(0);
    this.formConsultaGeneral.controls.negocioId.setValue(0);
    this.selectionChange_negocios(0);
    this.formConsultaGeneral.controls.marcaVehiculoId.setValue(0);
    this.selectionChange_marcas(0);
    this.formConsultaGeneral.controls.anioVehiculo.setValue(0);
    this.formConsultaGeneral.controls.usoVehiculoId.setValue(0);
    this.formConsultaGeneral.controls.estadoVehiculoId.setValue(0);
    this.formConsultaGeneral.controls.companiaId.setValue(0);
    this.formConsultaGeneral.controls.ramoId.setValue(0);
    this.selectionChange_CompaniaRamo();
    this.dataSource = new MatTableDataSource();
  }

  limpiarConsultaEspecifica() {
    this.formConsultaEspecifica.reset();
    this.dataSource = new MatTableDataSource();
  }

  errorCallBack() {
    this.snackBar.open(this.l('consult.production.callback.select'), '', { duration: this.snackBarDuration });
  }

  agregarItemAll(result: FiltroGeneralOutputDto[]) {
    if (!result) {
      result = [];
    }
    result.splice(0, 0, this.defaultItemSelect);
    return result;
  }

  cargarFiltrosConsultaGeneral() {
    // Fechas
    this._consultaProduccionService.filtroTipoFecha().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroTipoFecha = this.agregarItemAll(result);
      if (this.filtroTipoFecha && this.filtroTipoFecha.length > 1) {
        this.formConsultaGeneral.controls.tipoFecha.setValue(this.filtroTipoFecha[1].id);
      } else {
        this.formConsultaGeneral.controls.tipoFecha.setValue(this.filtroTipoFecha[0].id);
      }
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    // Documentos
    this._consultaProduccionService.filtroEstadoPoliza().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroEstadoPoliza = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.estadoPolizaId.setValue(0);
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    // Datos comerciales
    this._consultaProduccionService.filtroNegocios().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroNegocios = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.negocioId.setValue(0);
      this.selectionChange_negocios(0);
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    // Datos vehiculos
    this._consultaProduccionService.filtroMarcas().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroMarcas = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.marcaVehiculoId.setValue(0);
      this.selectionChange_marcas(0);
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    this._consultaProduccionService.filtroAos().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroAnnos = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.anioVehiculo.setValue(0);
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    this._consultaProduccionService.filtroUsoEspecificosVeh().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroUsoEspecifico = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.usoVehiculoId.setValue(0);
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    this._consultaProduccionService.filtroEstadoVehiculo().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroEstado = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.estadoVehiculoId.setValue(0);
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

    // Datos Emision
    this._consultaProduccionService.filtroCompaias().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroCompannias = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.companiaId.setValue(0);
      this.selectionChange_CompaniaRamo();
    }, err => { this.errorCallBack(); });

    this._consultaProduccionService.filtroRamos().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroRamos = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.ramoId.setValue(0);
      this.selectionChange_CompaniaRamo();
    },
    err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );

  }

  selectionChange_negocios(select: any) {
    this._consultaProduccionService.filtroCanales(select).subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroCanales = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.canalId.setValue(0);
    }, err => { this.errorCallBack(); },
    () => {
      this.selectionChange_canales(0);
      this.loadingPending--;
    });
  }

  selectionChange_canales(select: any) {
    this._consultaProduccionService.filtroSucursales(select).subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroSucursales = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.sucursalId.setValue(0);
    }, err => { this.errorCallBack(); },
    () => {
      this.selectionChange_sucursales(0);
      this.loadingPending--;
    });
  }

  selectionChange_sucursales(select: any) {
    this._consultaProduccionService.filtroAgentes(select).subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroAgentes = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.agenteId.setValue(0);
    }, err => { this.errorCallBack(); },
    () => { this.loadingPending--; }
    );
  }

  selectionChange_marcas(select: any) {
    this._consultaProduccionService.filtroModelos(select).subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroModelos = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.modeloVehiculoId.setValue(0);
    }, err => { this.errorCallBack(); },
    () => { this.loadingPending--; });
  }

  selectionChange_CompaniaRamo() {
    const compania = this.formConsultaGeneral.controls.companiaId.value || -1;
    const ramo = this.formConsultaGeneral.controls.ramoId.value || -1;
    this._consultaProduccionService.filtroPlanes(compania, ramo).subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroPlanes = this.agregarItemAll(result);
      this.formConsultaGeneral.controls.planId.setValue(0);
    }, err => { this.errorCallBack(); },
    () => { this.loadingPending--; });
  }

  cargarFiltrosConsultaEspecifica() {
    // Datos Persona
    this._consultaProduccionService.filtroTipoPersona().subscribe((result: FiltroGeneralOutputDto[]) => {
      this.filtroTipoPersona = this.agregarItemAll(result);
      this.formConsultaEspecifica.controls.tipoPersonaId.setValue(0);
    }, err => { this.errorCallBack(); },
    () => { this.loadingPending--; });
  }

  tipoPersonaChange(tipoPersona: number) {
    this.formConsultaEspecifica.controls.tipoPersonaId.setValidators(
      [tipoPersonaValidator(this.formConsultaEspecifica.controls.nroDocumentoPersona.value,
        this.formConsultaEspecifica.controls.nombreCompletoPersona.value,
        tipoPersona)]
    );
    this.formConsultaEspecifica.controls.tipoPersonaId.updateValueAndValidity();
  }

  rutChange(rut: string) {
    this.formConsultaEspecifica.controls.tipoPersonaId.setValidators(
      [tipoPersonaValidator(rut, this.formConsultaEspecifica.controls.nombreCompletoPersona.value,
        this.formConsultaEspecifica.controls.tipoPersonaId.value)]
    );
    this.formConsultaEspecifica.controls.tipoPersonaId.updateValueAndValidity();
    this.formConsultaEspecifica.controls.tipoPersonaId.markAsTouched();
  }

  nombreChange(nombre: string) {
    this.formConsultaEspecifica.controls.tipoPersonaId.setValidators(
      [tipoPersonaValidator(nombre, this.formConsultaEspecifica.controls.nroDocumentoPersona.value,
        this.formConsultaEspecifica.controls.tipoPersonaId.value)]
    );
    this.formConsultaEspecifica.controls.tipoPersonaId.updateValueAndValidity();
    this.formConsultaEspecifica.controls.tipoPersonaId.markAsTouched();
  }

  clearObject(json: any) {
    return JSON.parse(JSON.stringify(json, (key, value) => {
      if (value === null || value === undefined || value === '') { return undefined; }
      return value;
    }));
  }

  existenPolizas(): boolean {
    return this.dataSource && this.dataSource.data && this.dataSource.data.length > 0;
  }

  primeraConsulta() {
    return this.contadorConsultas === 0;
  }

  onPageSwitch(event: any) {
    this.paginatorPageSize = event.pageSize;
    this.paginatorPageIndex = event.pageIndex;

    if (this.criterioGeneral === 0) {
      this.onSubmit(this.formConsultaGeneral.value, this.criterioGeneral, event.pageIndex, event.pageSize);
    } else {
      this.onSubmit(this.formConsultaEspecifica.value, this.criterioGeneral, event.pageIndex, event.pageSize);
    }
  }

  onSubmit(formValue: ConsultaProduccionInputDto, criterioGeneral: number, nroPagina: number, nroRegistros: number) {
    const that = this;
    this.criterioGeneral = criterioGeneral;
    this.dataSource = new MatTableDataSource();
    this.loading = true;

    formValue.criterioGeneral = this.criterioGeneral;
    formValue.nroPagina = nroPagina;
    formValue.nroRegistros = nroRegistros;
    this.formCache = this.clearObject(formValue);
    this._consultaProduccionService.listar(this.formCache)
      .subscribe(
          (result) => {
              this.data = result.items;
              this.dataSource = new MatTableDataSource(this.mappingResultadoConsulta(result.items));
              if (result.items.length === 0) {
                this.snackBar.open(this.l('consult.production.callback.find.empty'), '', { duration: this.snackBarDuration });
              } else {
                this.paginatorLength = result.items[0].totalPaginas;
              }
            },
          err => {
            this.loading = false;
            this.snackBar.open(this.l('consult.production.callback.find.error'), '', { duration: this.snackBarDuration });
          },
          () => {
            this.contadorConsultas++;
            this.loading = false;
            setTimeout(() => {
                  if (this.existenPolizas()) {
                    this.dataSource.sort = this.sort;
                  }
                  if (that.endtable) {
                     that.endtable.nativeElement.scrollIntoView();
                  }
            }, 500);
          }
      );
  }

  exportar() {
    this.loading = true;

    this.formCache.criterioGeneral = this.criterioGeneral;
    this.formCache.nroPagina = 0;
    this.formCache.nroRegistros = this.paginatorLength;

    this._consultaProduccionService.exportarExcel(this.formCache)
      .subscribe(
          (result) => {
              this.crearExcel(result.items);
              if (result.items.length === 0) {
                this.snackBar.open(this.l('consult.production.callback.find.empty'), '', { duration: this.snackBarDuration });
              }
            },
          err => {
            this.loading = false;
            this.snackBar.open(this.l('consult.production.callback.find.error'), '', { duration: this.snackBarDuration });
          },
          () => {
            this.loading = false;
          }
      );
  }

  crearExcel(result: ConsultaProduccionOutputDto[]) {
    const headingFormat = [
      [
        '#',
        this.l('consult.production.table.header.policy'),
        this.l('consult.production.table.header.company'),
        this.l('consult.production.table.header.trade'),
        this.l('consult.production.table.header.status'),
        this.l('consult.production.table.header.startvalidity'),
        this.l('consult.production.table.header.endvalidity'),
        this.l('consult.production.table.header.contractingname'),
        this.l('consult.production.table.header.contractingnro'),
        this.l('consult.production.table.header.executive'),
        this.l('consult.production.table.header.channel'),
        this.l('consult.production.table.header.branchoffice')
      ]
    ];
    const heading = '';
    const ocultarColumnas: number[] = [12, 13];
    this.exportExcelComponent.ExportTOExcelList(headingFormat, heading, this.mappingResultadoConsulta(result),
      'Polizas', 'ListadoPolizas', ocultarColumnas);
  }

  mappingResultadoConsulta(result: ConsultaProduccionOutputDto[]) {
    let nro = 0;
    const mapping = result.map(o => {
      nro ++;
      return {
        nro: nro,
        nroPoliza: o.nroPoliza,
        compania: o.compania,
        ramo: o.ramo,
        estado: o.estado,
        fechaVigenciaPolizaIni: o.fechaVigenciaPolizaIni.format('DD/MM/YYYY'),
        fechaVigenciaPolizaFin: o.fechaVigenciaPolizaFin.format('DD/MM/YYYY'),
        nombreContratante: o.nombreContratante,
        nroDocumentoContratante: new RutPipe().transform(o.nroDocumentoContratante),
        agente: o.agente,
        canal: o.canal,
        sucursal: o.sucursal,
        polizaId: o.polizaId,
        companiaId: o.companiaId
      };
    });
    return mapping;
  }

  validaDialogCambiarEstado(): boolean {
    let ok = true;
    const compaiasFiltradas = Array.from(new Set(this.selection.selected.map(x => { return x.companiaId })));
    if (this.selection.selected.length === 0) {
      ok = false;
      this.snackBar.open(this.l('consult.changestatus.emptyselected'), '', { duration: this.snackBarDuration });
    } else if (compaiasFiltradas.length > 1) {
      ok = false;
      this.snackBar.open(this.l('consult.changestatus.onecompany'), '', { duration: this.snackBarDuration });
    }

    return ok;
  }

  openDialogCambiarEstado() {
    if (this.validaDialogCambiarEstado()) {
      const dialogRef = this.dialog.open(CambiarestadoComponent, {
        width: this.tamaoPopup,
        data: {
          selected: this.selection.selected || [],
          form: null
        }
      });

      dialogRef.afterClosed().subscribe(
        (result: DialogData) => {
            if (result) {
              const dialogRefConfirmacion = this.dialog.open(CambiarestadoConfirmacionComponent, {
                width: this.tamaoPopupConfirmacion,
                data: result
              });

              dialogRefConfirmacion.afterClosed().subscribe((confirmacion) => {
                if (confirmacion) {
                  this.snackBar.open(this.l('consult.changestatus.modify.part1') + ' '
                  + this.selection.selected.length + ' '
                  + this.l('consult.changestatus.modify.part2'), '',
                  { duration: this.snackBarDuration });
                }
              });
            }
        }
      );
    }
  }

  updateValueFechaDesde(fecha: any) {
    this.formConsultaGeneral.controls.fechaDesde.setValidators(
      [rangoFechasValidator(fecha, this.formConsultaGeneral.controls.fechaHasta.value)]
    );
    this.formConsultaGeneral.controls.fechaDesde.updateValueAndValidity();
  }

  updateValueFechaHasta(fecha: any) {
    this.formConsultaGeneral.controls.fechaHasta.setValidators(
      [rangoFechasValidator(this.formConsultaGeneral.controls.fechaDesde.value, fecha)]
    );
    this.formConsultaGeneral.controls.fechaHasta.updateValueAndValidity();
  }

  // tslint:disable-next-line:member-ordering
  compareFn: ((f1: any, f2: any) => boolean) | null = this.compareByValue;
  compareByValue(f1: any, f2: any) {
    return f1 && f2 && f1 === f2;
  }

}
