import * as XLSX from 'xlsx';
import { AppComponentBase } from '@shared/app-component-base';
import { reduce } from 'rxjs/operators';
import { style } from '@angular/animations';

export class ExportExcelComponent extends AppComponentBase 
{
 ExportTOExcelList(HeadingFomart,heading,value,nameHoja,nameFile,hiddenColumn)//cabecera y lista
{
    /* Se asigna el nombre de las columnas */
      var ws = XLSX.utils.aoa_to_sheet(HeadingFomart);
      
      /* Se asigna las columnas y los datos */
      
      XLSX.utils.sheet_add_json(ws, value
        ,{
        header:heading,
        origin:1, //deja el origen de las columnas en la primer fila
        skipHeader:true //Permite el nombre de columnas formateados, en false tal como viene el objeto por defecto
        }
       );
       
      /*se crea el libro */
      const wb: XLSX.WorkBook = XLSX.utils.book_new();
      
      /*Se asigna el libro, la hoja con datos y el nombre de la hoja */
      XLSX.utils.book_append_sheet(wb, ws, nameHoja);
    /*Para ocultar columnas*/ 
    ws['!cols'] = [];
    hiddenColumn.forEach(element => {
    ws['!cols'][element] = { hidden: true };
    });
   
   /**fin ocultar columnas */ 
  
      /* save to file */
      XLSX.writeFile(wb, nameFile+'.xlsx');
}
 ExportTOExcelTable(value,nameHoja,nameFile,hiddenColumn)//lista
{  
    /*Convierte datos en hoja de excel */
  const ws: XLSX.WorkSheet = XLSX.utils.table_to_sheet(value);
  /*se crea el libro */
  const wb: XLSX.WorkBook = XLSX.utils.book_new();
  /*Se asigna el libro, la hoja con datos y el nombre de la hoja */
  XLSX.utils.book_append_sheet(wb, ws, nameHoja);
  /*Para ocultar columnas*/ 
  ws['!cols'] = [];
  hiddenColumn.forEach(element => {
    ws['!cols'][element] = { hidden: true };
  });
   /**fin ocultar columnas */ 

      /* save to file */
      XLSX.writeFile(wb, nameFile+'.xlsx');
}
}