import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { FlujoDinamicoServiceProxy } from '@shared/service-proxies/service-proxies';

@Injectable()
export class FlujoDinamicoResolve implements Resolve<any> {

    constructor(private _flujoDinamicoServiceProxy: FlujoDinamicoServiceProxy) {}

    resolve(route: ActivatedRouteSnapshot) {
        return this._flujoDinamicoServiceProxy.obtener(Number(sessionStorage.getItem('flujoId')))
    }
}
