import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppComponentBase } from '@shared/app-component-base';
import { CotizacionCambioEstadoInputDto, CotizacionServiceProxy } from '@shared/service-proxies/service-proxies';
import { DialogData } from '../consultacotizacion.component';

@Component({
  selector: 'app-cambiarestadocotizacionconfirmacion',
  templateUrl: './cambiarestadocotizacionconfirmacion.component.html',
  styleUrls: ['./cambiarestadocotizacionconfirmacion.component.scss']
})
export class CambiarestadocotizacionConfirmacionComponent extends AppComponentBase implements OnInit {
  snackBarDuration = 3000;

  constructor(
    injector: Injector,
    private _cotizacionService: CotizacionServiceProxy,
    public dialogRef: MatDialogRef<CambiarestadocotizacionConfirmacionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private snackBar: MatSnackBar
  ) {
    super(injector);
  }

  ngOnInit() {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  cambiarEstado() {
    const cambiar: CotizacionCambioEstadoInputDto[] = [
      <CotizacionCambioEstadoInputDto> {
        cotizacionId: this.data.selected.map(o => {
          return o.cotizacionId;
        }),
        observacion: this.data.form.observacion,
        catTipoCambioEstado: this.data.form.estado,
        catMotivoCambioEstado: this.data.form.motivo
      }
    ];
    this._cotizacionService.cambiarEstado(cambiar).subscribe(
      (result: number) => {
        this.dialogRef.close(result);
      },
      err => {
        this.snackBar.open(this.l('consult.changestatus.saveerror'), '', { duration: this.snackBarDuration });
        this.dialogRef.close(0);
      }
    );
  }
}
