  displayedColumns: string[] = [
    'select',
    'nro',
    'nroCotizacion',
    'compania',
    'estado',
    'fechaVigenciaPolizaIni',
    'nombreContratante',
    'agente',
    'canal',
    'iconseleccion'
  ];
  dataSource: any;
  data: ConsultaCotizacionOutputDto[];
  selection = new SelectionModel<ConsultaCotizacionOutputDto>(true, []);
