    // Documentos
    filtroEstadoCotizacion: FiltroGeneralOutputDto[];
    // Datos comerciales
    filtroNegocios: FiltroGeneralOutputDto[];
    filtroCanales: FiltroGeneralOutputDto[];
    filtroSucursales: FiltroGeneralOutputDto[];
    filtroAgentes: FiltroGeneralOutputDto[];
