import { registerLocaleData } from '@angular/common';
import { LOCALE_ID, NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import {
  ConsultaCotizacionServiceProxy,
  ConsultaProduccionServiceProxy,
  CotizacionServiceProxy,
  FlujoDinamicoServiceProxy,
  MotorTarificacionServiceProxy,
  PolizaServiceProxy
} from '@shared/service-proxies/service-proxies';
import { FlujoDinamicoURLResolve } from './sales-flow/flujo-dinamico-url.resolve';
import { FlujoDinamicoResolve } from './sales-flow/flujo-dinamico.resolve';

import localesEsCL from '@angular/common/locales/es-CL';
registerLocaleData(localesEsCL);

import { routes } from '@app/app.routing';
import { AppComponents, AppEntryComponents, AppExports, AppImports } from './app.barrel';

@NgModule({
  imports: [...AppImports, RouterModule.forChild(routes)],
  exports: AppExports,
  declarations: [...AppComponents, ...AppEntryComponents],
  entryComponents: AppEntryComponents,
  providers: [
    FlujoDinamicoServiceProxy,
    MotorTarificacionServiceProxy,
    CotizacionServiceProxy,
    ConsultaProduccionServiceProxy,
    ConsultaCotizacionServiceProxy,
    PolizaServiceProxy,
    FlujoDinamicoResolve,
    FlujoDinamicoURLResolve,
    {
      provide: LOCALE_ID,
      useValue: 'es-CL'
    }
  ]
})
export class AppModule {}
