import { Component, Inject, Injector, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppComponentBase } from '@shared/app-component-base';
import { ConsultaCotizacionServiceProxy, FiltroGeneralOutputDto } from '@shared/service-proxies/service-proxies';
import { DialogData, DialogDataForm } from '../consultacotizacion.component';

@Component({
  selector: 'app-cambiarestadocotizacion',
  templateUrl: './cambiarestadocotizacion.component.html',
  styleUrls: ['./cambiarestadocotizacion.component.scss']
})
export class CambiarestadocotizacionComponent extends AppComponentBase implements OnInit {
  formCambiarEstado: FormGroup;
  filtroEstado: FiltroGeneralOutputDto[];
  filtroMotivo: FiltroGeneralOutputDto[];
  snackBarDuration = 3000;

  constructor(
    injector: Injector,
    private _consultaCotizacionService: ConsultaCotizacionServiceProxy,
    private formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<CambiarestadocotizacionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private snackBar: MatSnackBar
  ) {
    super(injector);
  }

  ngOnInit() {
    this.formCambiarEstado = this.formBuilder.group({
      estado: [null, [Validators.required]],
      motivo: [null, [Validators.required]],
      observaciones: [null, [Validators.required, Validators.maxLength(250)]]
    });

    this._consultaCotizacionService.filtroCambioEstadoCotizacion().subscribe(
      (result: FiltroGeneralOutputDto[]) => {
        this.filtroEstado = result;
      },
      err => {
        this.snackBar.open(this.l('consult.changestatus.loaderror'), '', { duration: this.snackBarDuration });
      }
    );
  }

  selectionChange_estado(id: number) {
    // const companiaId = this.data.selected.map(o => { return o.companiaId })[0];
    this._consultaCotizacionService.filtroMotivo(id, 1).subscribe(
      (result: FiltroGeneralOutputDto[]) => {
        console.log(result);
        this.filtroMotivo = result;
      },
      err => {
        this.snackBar.open(this.l('consult.changestatus.loaderror'), '', { duration: this.snackBarDuration });
      }
    );
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  onSubmit(value: DialogDataForm) {
    this.data.form = value;
    this.data.form.estadoDescripcion = this.filtroEstado.filter(item => item.id === this.data.form.estado)[0].nombre;
    this.dialogRef.close(this.data);
  }
}
