import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { AfterViewInit, Component, Injector, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import { AppComponentBase } from '@shared/app-component-base';
import { AppAuthService } from '@shared/auth/app-auth.service';
import { MenuItem } from '@shared/layout/menu-item';
import { ListResultDtoOfMenuDto, MenuDto, SessionServiceProxy } from '@shared/service-proxies/service-proxies';
import { AppSessionService } from '@shared/session/app-session.service';
import { filter } from 'lodash';
import { Observable } from 'rxjs';
import { finalize, map } from 'rxjs/operators';

@Component({
  templateUrl: './app.component.html',
  animations: [appModuleAnimation()],
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent extends AppComponentBase implements OnInit, AfterViewInit {
  languages: abp.localization.ILanguageInfo[];
  currentLanguage: abp.localization.ILanguageInfo;
  status = false;
  userName: string;
  rolName: string;
  showMenu: any;

  opcionesMenuPerfil = [
    {
      menuId: 'updateAccount',
      menuText: 'Administrar mi cuenta',
      action: 'openUserProfile',
      desc: 'Actualizar mi perfil',
      icon: 'settings'
    },
    { menuId: 'logout', menuText: 'Logout', action: 'logout', desc: 'Cerrar la sesin', icon: 'exit_to_app' }
  ];

  /*   opcionesMenuNotificaciones = [
    { menuText: 'Simulacin realizada con xito', action: '', desc: '11:25 (Hace 5 minutos)', icon: 'check_circle' },
    { menuText: 'Se agreg el nuevo mandato de la compaia Liberty Seguros', action: '', desc: '8:34 (Hace 3 horas)', icon: 'description' },
    { menuText: 'Simulacin realizada con xito', action: '', desc: 'ayer', icon: 'check_circle' },
    {
      menuText: 'Lunes a partir de las 22 horas el sistema estar en mantenimiento por 1 hora.',
      action: '',
      desc: '21 oct',
      icon: 'insert_chart'
    }
  ];

  opcionesMenuAyuda = [
    { menuText: 'Configuracin de la cuenta', action: '', desc: '', icon: 'assessment' },
    { menuText: 'Configuracin de los widgets visibles en el escritorio', action: '', desc: '', icon: 'assessment' },
    { menuText: 'Administrar cuentas y usuarios', action: '', desc: '', icon: 'assessment' },
    { menuText: 'Herramientas de informe', action: '', desc: '', icon: 'assessment' }
  ]; */

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset).pipe(map(result => result.matches));

  menuItems: MenuItem[];
  menu: MenuDto[];

  constructor(
    injector: Injector,
    private router: Router,
    private breakpointObserver: BreakpointObserver,
    private _authService: AppAuthService,
    private _sessionService: SessionServiceProxy,
    private _appSessionService: AppSessionService
  ) {
    super(injector);
  }

  ngOnInit(): void {
    this.languages = filter(this.localization.languages, l => !l.isDisabled);
    this.currentLanguage = this.localization.currentLanguage;
    this.currentLanguage.name = abp.utils.getCookieValue('Abp.Localization.CultureName');
    this.showMenu = sessionStorage.getItem('showMenu') ? sessionStorage.getItem('showMenu') : 'true';
    this.userName = this._appSessionService.getShownLoginName();
    this.rolName = this._appSessionService.getShownRolName();
    if (this.showMenu !== 'false') {
      this._sessionService
        .getMenu()
        .pipe(
          finalize(() => {
            // finishedCallback();
          })
        )
        .subscribe(
          (result: ListResultDtoOfMenuDto) => {
            this.menu = result.items;
            this.getMenuItems();
          },
          err => {
            console.log(err);
          },
          () => {
            console.log('Done loading');
          }
        );
    }
  }

  ngAfterViewInit(): void {}

  logout(): void {
    this._authService.logout();
  }

  call(n: string): void {
    if (n === 'logout') {
      this.logout();
    } else if (n === 'openUserProfile') {
      this.router.navigate(['/app/perfil']);
    }
  }

  changeLanguage(languageName: string): void {
    abp.utils.setCookieValue(
      'Abp.Localization.CultureName',
      languageName,
      new Date(new Date().getTime() + 5 * 365 * 86400000), // 5 year
      abp.appPath
    );
    location.reload();
  }

  getMenuItems(): void {
    let subMenuItems: MenuItem[];
    this.menuItems = [];
    this.menu.forEach(element => {
      if (element.parentMenu === 0) {
        subMenuItems = [];
        this.menu.forEach(subElement => {
          if (subElement.parentMenu === element.id) {
            subMenuItems.push(new MenuItem(this.l(subElement.name), subElement.permissionName, subElement.icon, subElement.route));
          }
        });
        this.menuItems.push(new MenuItem(this.l(element.name), element.permissionName, element.icon, element.route, subMenuItems));
      }
    });
    console.log(this.menuItems);
  }

  showMenuItem(menuItem: MenuItem): boolean {
    if (this.permission.isGranted('ADMIN')) {
      return true;
    }

    if (menuItem.permissionName) {
      return this.permission.isGranted(menuItem.permissionName);
    }

    return true;
  }

  ActiveMenu() {
    this.status = !this.status;
  }
  menuHighlight(id: string) {
    const clase = document.getElementsByClassName('menu-list-item');
    Array.prototype.forEach.call(clase, function(el) {
      document.getElementById(el.id).classList.remove('menu-highligth');
      document.getElementById(el.id).style.color = '#8c8c8c';
    });
    document.getElementById(id).classList.add('menu-highligth');
    document.getElementById(id).style.color = 'green';
  }
}
