import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AppComponentBase } from '@shared/app-component-base';
import { ConsultaProduccionServiceProxy, PolizaCuotaBitacoraOutputDto } from '@shared/service-proxies/service-proxies';

@Component({
  selector: 'app-bitacoracuota',
  templateUrl: './bitacoracuota.component.html',
  styleUrls: ['./bitacoracuota.component.scss']
})
export class BitacoracuotaComponent extends AppComponentBase implements OnInit {
  dataSource: PolizaCuotaBitacoraOutputDto[];
  displayedColumns: string[] = ['fecha', 'hora', 'motivo', 'descripcion'];

  constructor(
    injector: Injector,
    private _consultaProduccionService: ConsultaProduccionServiceProxy,
    public dialogRef: MatDialogRef<BitacoracuotaComponent>,
    @Inject(MAT_DIALOG_DATA) public idCuota: number
  ) {
    super(injector);
  }

  ngOnInit() {
    this._consultaProduccionService.bitacoraCuotaPoliza(this.idCuota).subscribe(
      (result: PolizaCuotaBitacoraOutputDto[]) => {
        this.dataSource = result;
      },
      err => {},
      () => {}
    );
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}
