import { Component, Inject, Injector, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppComponentBase } from '@shared/app-component-base';
import { PolizaCambioEstadoInputDto, PolizaServiceProxy } from '@shared/service-proxies/service-proxies';
import { DialogData } from '../produccion/produccion.component';

@Component({
  selector: 'app-cambiarestado-confirmacion',
  templateUrl: './cambiarestado-confirmacion.component.html',
  styleUrls: ['./cambiarestado-confirmacion.component.scss']
})
export class CambiarestadoConfirmacionComponent extends AppComponentBase implements OnInit {
  snackBarDuration = 3000;
  result = 0;
  loading = false;

  constructor(
    injector: Injector,
    private _polizaService: PolizaServiceProxy,
    public dialogRef: MatDialogRef<CambiarestadoConfirmacionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private snackBar: MatSnackBar
  ) {
    super(injector);
  }

  ngOnInit() {}

  estadoDescripcion() {
    return this.data && this.data.form ? this.data.form.estadoDescripcion : '';
  }

  selectedLength() {
    return this.data && this.data.selected ? this.data.selected.length : 0;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  enviarCambio(callback: (status: number) => void) {
    const cambiar: PolizaCambioEstadoInputDto[] = [
      <PolizaCambioEstadoInputDto> {
        polizaId: this.data.selected.map(o => {
          return o.polizaId;
        }),
        observacion: this.data.form.observacion,
        catTipoCambioEstado: this.data.form.estado,
        catMotivoCambioEstado: this.data.form.motivo
      }
    ];
    this._polizaService.cambiarEstado(cambiar).subscribe(
      (result: number) => {
        this.result = result;
        callback(result);
      },
      err => {
        this.snackBar.open(this.l('consult.changestatus.saveerror'), '', { duration: this.snackBarDuration });
        callback(0);
      },
      () => {
        this.loading = false;
      }
    );
  }

  cambiarEstado() {
    this.loading = true;
    this.enviarCambio(result => {
      this.dialogRef.close(result);
    });
  }
}
