import { Component, OnInit, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { PlusNotificatorComponent } from '@shared/plus-notificator/plus-notificator.component';
import { MatSnackBar } from '@angular/material/snack-bar';
// import { FormularioDinamicoServiceProxy, LauncherFlujoOutputDto } from '../../../shared/service-proxies/service-proxies';
import { AppComponentBase } from '@shared/app-component-base';
import { FlujoDinamicoServiceProxy, ListarFlujosPorSucursalOutputDto } from '@shared/service-proxies/service-proxies';

@Component({
  selector: 'app-launcher',
  templateUrl: './launcher.component.html',
  styleUrls: ['./launcher.component.scss']
})
export class LauncherComponent extends AppComponentBase implements OnInit {

  // LauncherSucursales = new FormControl('');
  notifyComp: PlusNotificatorComponent;
  tipoDeFlujo: string;
  // flujos: LauncherFlujoOutputDto[];
  flujos: any;
  sucursales: any;
  selectSucursales = new FormControl('');

/*    sucursales = [
    {id: '1', displayName: 'Sucursal 1111'},
    {id: '2', displayName: 'Sucursal 2222'},
    {id: '3', displayName: 'Sucursal 3333'},
    {id: '4', displayName: 'Sucursal 4444'}
  ]; */

  constructor(private _notificator: MatSnackBar,
    private _formularioDinamicoServiceProxy: FlujoDinamicoServiceProxy,
      injector: Injector) {
      super(injector);
      this.notifyComp = new PlusNotificatorComponent(this._notificator);
    // this._launcherFlujoServiceProxy = launcherFlujoServiceProxy;
  }

  ngOnInit() {
    this.sucursales = JSON.parse(sessionStorage.getItem('sucursales'));
    this.selectSucursales.setValue(Number(sessionStorage.getItem('sucursalId')));
    // this.cargaSucursales(sessionStorage.getItem('sucursalId'));
    this.cargaSucursales(1);
    // sessionStorage.setItem('flujoId', '1');
    console.log('>>> LAUNCHER');
  }

/*   ngAfterViewInit(): void {
    this.cargaSucursales(1);
  } */

  cargaSucursales(id: any) {
    this.flujos = new ListarFlujosPorSucursalOutputDto;
    this._formularioDinamicoServiceProxy.listarFlujosPorSucursal(id).subscribe(r => {
      this.flujos = r;
    });
  }

  routeToFlow(flowId: string): void {
    this.notifyComp.success('ID DE FLUJO : : :: : : ' + flowId, 'ok');
  }

  loadFlows(value: any) {
    this.notifyComp.success('value : : :: : : ' + value, 'ok');
    /* this.cargaSucursales(value); */
  }

  simulationSearch(id: number) {
    sessionStorage.setItem('flujoId', id.toString());

  }
}
