import { MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material/dialog';
import { CustomDialogComponent } from './custom-dialog.component'

export class PlusDialog {
  constructor(public dialog: MatDialog) {}
  open(title: string, msg: string): void {
    const dialogRef = this.dialog.open(CustomDialogComponent, {
      width: '250px',
      data: {type: 'success', title: title, msg: msg}
    });

    // dialogRef.afterClosed().subscribe(result => {
    // });
  }
  openConfirm(title: string, msg: string): MatDialogRef<CustomDialogComponent> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;

    dialogConfig.data = {
        type: 'warning',
        title: title,
        msg: msg
    };
    const dialogRef =  this.dialog.open(CustomDialogComponent, dialogConfig);

    return  dialogRef;
  }
}






